import { Mark, Data } from '@ali/4ever-cangjie';

export interface ColorData extends Data {
  value: string;
}

interface Color extends Mark<ColorData> {}

const Color = {
  TYPE: 'color',

  create(color: string): Color {
    return Mark.create<ColorData>({
      type: Color.TYPE,
      data: { value: color },
    });
  },

  isColor(maybeColor: any): maybeColor is Color {
    return Mark.isMark(maybeColor) && maybeColor.type === Color.TYPE;
  },
};

export default Color;
